export const isAnyNoteActiveFunc = (allNotes: any, setIsAnyNoteActive: any) => {
  const tempCheck = allNotes.every((v: any) => {
    return v.deleted;
  });
  setIsAnyNoteActive(!tempCheck);
};

export const isRecycleBinEmptyFunc = (
  allNotes: any,
  setIsRecycleBinEmpty: any
) => {
  const tempCheck = allNotes.find((v: any) => {
    return v.deleted === true;
  });
  setIsRecycleBinEmpty(!!!tempCheck);
};

export async function schedulePushNotification(
  note: any,
  type: string | boolean,
  title: string
) {
  if (JSON.parse(localStorage.getItem("notifications") || "") !== "On") {
    return;
  }
  const reg = await navigator.serviceWorker.getRegistration();
  const allNotifications = await reg?.getNotifications({
    // @ts-ignore
    includeTriggered: true,
  });
  for (let i = note.revisionNumber + 1; i < note.revisions.length; i++) {
    const revisionDate = new Date(note.revisions[i]);
    const notificationObj = allNotifications?.find(
      (v) => v.tag === format(revisionDate, "dd-MM-yyyy")
    );
    let body = "raw";
    if (notificationObj) {
      const notificationObjBody = notificationObj?.body;
      if (type === "delete") {
        if (notificationObjBody.includes("üóíÔ∏è " + note.title + " üìñ" + "\n")) {
          body = notificationObjBody.replace(
            "üóíÔ∏è " + note.title + " üìñ" + "\n",
            ""
          );
        } else if (
          notificationObjBody.includes("\n" + "üóíÔ∏è " + note.title + " üìñ")
        ) {
          body = notificationObjBody.replace(
            "\n" + "üóíÔ∏è " + note.title + " üìñ",
            ""
          );
        } else if (notificationObjBody.includes("üóíÔ∏è " + note.title + " üìñ")) {
          body = notificationObjBody.replace("üóíÔ∏è " + note.title + " üìñ", "");
        }
      } else if (type === "edit") {
        if (notificationObjBody.includes("üóíÔ∏è " + note.title + " üìñ" + "\n")) {
          body = notificationObjBody.replace(
            "üóíÔ∏è " + note.title + " üìñ" + "\n",
            "üóíÔ∏è " + title + " üìñ" + "\n"
          );
        } else if (
          notificationObjBody.includes("\n" + "üóíÔ∏è " + note.title + " üìñ")
        ) {
          body = notificationObjBody.replace(
            "\n" + "üóíÔ∏è " + note.title + " üìñ",
            "\n" + "üóíÔ∏è " + title + " üìñ"
          );
        } else if (notificationObjBody.includes("üóíÔ∏è " + note.title + " üìñ")) {
          body = notificationObjBody.replace(
            "üóíÔ∏è " + note.title + " üìñ",
            "üóíÔ∏è " + title + " üìñ"
          );
        }
      } else {
        body = "üóíÔ∏è " + title + " üìñ" + "\n" + notificationObjBody;
      }
    } else if (!type) {
      body = "üóíÔ∏è " + title + " üìñ";
    }
    const trigger =
      body === ""
        ? "close"
        : differenceInSeconds(add(revisionDate, { hours: 6 }), new Date()) *
          1000;

    if (trigger === "close") {
      notificationObj?.close();
    } else if (Math.sign(trigger) !== -1) {
      notificationObj?.close();
      if (Notification.permission !== "granted") {
      } else {
        reg?.showNotification(
          "Review your notes, so you Never Forget them! üìî",
          {
            tag: format(revisionDate, "dd-MM-yyyy"), // a unique ID
            body: body, // content of the push notification
            // @ts-ignore
            showTrigger: new TimestampTrigger(new Date().getTime() + trigger), // set the time for the push notification
            badge: logoInBase64,
            icon: logoInBase64,
          }
        );
      }
    }
  }
}

export const logoInBase64 =
  "data:image/png;base64,";
